body {
  background: rgb(245, 245, 245);
}

.App-header {
  display: flex;
  justify-content: space-between;
  width: calc(100% - 14px);
  height: 70px;
  padding: 0 7px;
  box-shadow: 0 0 10px 1px rgb(0, 0, 0, 0.1);
  background: rgb(244, 244, 244);
}

#img-logo {
  position: relative;
  top: 6px;
  left: 0;
  width: 56px;
  height: 56px;
  border-radius: 10px;
  border: 1px solid rgb(226, 226, 226);
}

.menu-btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: all;
  top: 6px;
  width: 56px;
  height: 56px;
  border: 1px solid rgb(220, 220, 220);
  border-radius: 10px;
  background: rgb(255, 255, 255);
}

.menu-btn.disabled {
  opacity: 0.5;
  pointer-events: none;
}

.menu-btn-text {
  position: relative;
  top: -5px;
  font-size: 41px;
  font-weight: bold;
}

.item {
 
  pointer-events: all;
  margin: 15px;
  padding: 10px 15px;
  border-radius: 10px;
  box-shadow: 0 0 5px 1px rgb(0, 0, 0, 0.05);
  background: rgb(255, 255, 255);
}

.item.disable {
  pointer-events: none;
}

.item-title {
  font-weight: bold;
  padding: 2px 0;
}

.item-desc {
  font-size: 12px;
}

.modal {
  display: none;
  position: absolute;
  padding: 10px;
  top: 150px;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 20px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  overflow: scroll;
  background: rgb(218, 219, 232);
}

.modal.is-show {
  display: block;
}

.btn-select {
  width: calc(100% - 30px);
  margin: 15px;
  padding: 12px 10px;
  border-radius: 10px;
  border: none;
  background: rgb(228, 228, 228);
}

.btn-select:hover {
  background: rgb(217, 217, 217);
}

.btn-sel {
  width: calc(50% - 10px);
  margin: 5px;
  display: inline;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 12px;
  border-radius: 15px;
  border: none;
}

.btn-sel.is-active {
  background: rgb(168, 181, 238);
}

#list {
  overflow: scroll;
}

#list-header {
  display: flex;
  top: -10px;
  position: sticky;
  height: 60px;
  align-items: center;
  justify-content: center;
  background: rgb(218, 219, 232);
}

.list-btn {
  width: calc(100% - 10px);
  border: none;
  padding: 15px;
  margin: 5px;
  font-size: 15px;
  font-weight: bold;
  border-radius: 10px;
}

.one-lesson {
  position: relative;
  border: 1px solid rgb(212, 212, 212);
  margin: 10px 15px;
  padding: 10px;
  border-radius: 10px;
  background: rgb(235, 236, 243);
}

.one-number {
  position: absolute;
  left: 5px;
  color: red;
  font-weight: bold;
}

.one-space {
  margin-left: 10px;
}

.day-name {
  display: block;
  font-size: 15px;
  font-weight: bold;
  margin-top: 20px;
  margin-left: 15px;
  margin-bottom: 10px;
}

.day-name.hide {
  display: none;
}

.a-list {
  position: absolute;
  top: 130px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: scroll;
}

.b-list {
  overflow: scroll;
}

.scroll-main {
  overflow: hidden;
}

.scroll-sub {
  position: absolute;
  top: 70px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: scroll;
}

.title {
  padding: 0 15px;
}

#no-browser {
  width: 300px;
  display: block;
  text-align: center;
  margin: 10px auto;
  padding: 10px;
  border-radius: 10px;
  background: rgb(226, 226, 243);
}

#no-browser-warning {
  color: white;
  font-size: 17px;
  font-weight: bold;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: rgb(243, 23, 23);
}
